import { Component, OnInit } from '@angular/core';
import { EmployeeDataService } from '../service/employee-data.service';
import { Employee } from '../interface/employee';
import { MatDialog } from "@angular/material/dialog";
import { AddEmployeeComponent } from '../add-employee/add-employee.component';

@Component({
  selector: 'app-name-tab',
  templateUrl: './name-tab.component.html',
  styleUrls: ['./name-tab.component.css']
})
export class NameTabComponent implements OnInit {

  empData: Employee[];
  selectedEmployee: Employee;
  id: number;
  name: string;
  company: string;
  phone: number;


  constructor(public dialog: MatDialog,
    private employeeService: EmployeeDataService) {
    this.employeeService.getEmployee();
  }

  ngOnInit() {
    this.getEmployee();
  }

  getEmployee(): void {
    this.employeeService.getAllEmployee().subscribe(emp => this.empData = emp);
  }

  onSelect(emp: Employee): void {
    this.selectedEmployee = emp;
  }

  //Add New Employee
  openDialog(): void {
    let next_id = this.getNextId();

    let emp = {
      id: next_id,
      name: this.name,
      company: this.company,
      phone: this.phone
    };

    const dialogRef = this.dialog.open(AddEmployeeComponent, {
      width: "250px",
      data: emp
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        let name = result.name;
        let company = result.company;
        let phone = result.phone;
        this.employeeService.addEmployee({ name,company,phone } as unknown as Employee)
          .subscribe(emp => {
            this.empData.push(emp);
          });
      }
    });
  }

  //GET NEXT ID
  getNextId():number{
    let id = this.empData.length != 0 ?  this.empData[this.empData.length-1].id + 1 : 1;
    return id;
  }

  //Edit Employee Details
  editEmp(emp:Employee){
    const dialogRef = this.dialog.open(AddEmployeeComponent, {
          width: "250px",
          data: emp
        });
    
        dialogRef.afterClosed().subscribe(result => {
          if (result) {
            this.employeeService.updateEmployee(result).subscribe();
          }
        });
  }
  //Delete Employee
  deleteEmp(empId: number, i:number){
    this.empData = this.empData.filter(e => e.id !== empId);
    this.employeeService.deleteEmployee(empId).subscribe();

    let max_i = this.empData.length;
    this.showNextEmpTab(i, max_i);
  }

  //display next emp-tab
  showNextEmpTab(i: number, max_i: number): void {
      if (max_i != 0) {
        i < max_i
          ? this.selectedEmployee = this.empData[i]
          : this.selectedEmployee = this.empData[0]
      } else {
        this.employeeService.noEmployee();
        this.selectedEmployee = undefined;
      }
    }

}
