import { Component, OnInit } from '@angular/core';
import { Subscription } from "rxjs";
import { EmployeeDataService } from '../service/employee-data.service';

@Component({
  selector: 'app-no-employee-data',
  templateUrl: './no-employee-data.component.html',
  styleUrls: ['./no-employee-data.component.css']
})
export class NoEmployeeDataComponent implements OnInit {

  message: any;
  subscription: Subscription;

  constructor(private employeeService: EmployeeDataService) { }

  ngOnInit() {
    this.subscription = this.employeeService.noEmpMessage.subscribe(mymessage => {
        this.message = mymessage;
      });
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

}
