import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { Employee } from '../interface/employee';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { catchError, map, tap } from 'rxjs/operators';
import { BehaviorSubject } from "rxjs";

@Injectable({
  providedIn: 'root'
})
export class EmployeeDataService {

  private employeeUrl = '//localhost:8080/cisco/employee';  // URL to web api
  employeeData: Employee[];
  public noEmpMessage = new BehaviorSubject("noEmp");

  httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
  };

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
  
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
  
      // TODO: better job of transforming error for user consumption
      console.log(`${operation} failed: ${error.message}`);
  
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

  constructor(private http: HttpClient) { }

  getAllEmployee(): Observable<Employee[]>{
    return this.http.get<Employee[]>(this.employeeUrl);
  }

  getEmployee(){
    this.getAllEmployee().subscribe( emp =>  {
      this.employeeData = emp;
      console.log(emp);
      if(this.employeeData.length === 0){
        this.noEmpMessage.next(undefined);
      }
    });
  }

  //Add new employee
  addEmployee(newEmp:Employee): Observable<Employee>{
    this.noEmpMessage.next("value");
    return this.http.post<Employee>(this.employeeUrl, newEmp, this.httpOptions).pipe(
      tap((newEmp: Employee) => console.log(`added Employee w/ id=${newEmp.id}`)),
      catchError(this.handleError<Employee>('addEmployee'))
    );
  }

  //Delete Employee
  deleteEmployee(empId: number): Observable<Employee>{
    const url = `${this.employeeUrl}/${empId}`;
    return this.http.delete<Employee>(url, this.httpOptions);
  }

  //Update Employee
  updateEmployee(emp:Employee): Observable<any>{
    const url = `${this.employeeUrl}/${emp.id}`;
    return this.http.put(url, emp, this.httpOptions).pipe(
      tap(_ => console.log(`updated hero id=${emp.id}`)),
      catchError(this.handleError<any>('updateHero'))
    );
  }

  //No Employee to show
  noEmployee():void{
      this.noEmpMessage.next(undefined);
    }
}
